$text-color: #fff;
// голубий
$main-color: #74c7d5;
$home-text-color: #74c7d5;
$twit-icon-color: #74c7d5;

$home-bg-color: #2c3e50;

// рожевий
$services-color: #b24a50;
$services-active-color: #df5c64;
$services-active-color: lighten(saturate($services-color, 25.91), 12.35);
$about-people-active-color: #df5c64;
