$about-block-active-color: darken($about-color, 7.25);
$about-people-icon-color: #5b4b59;
$about-people-icon-color: darken(desaturate(adjust-hue(#665464, 1), 0.04), 3.92);
$about-people-icon-color: darken( #665464, 3.9215686274509878);


$fb-icon-color: #4f689e;
$lin-icon-color: #3e61af;

// зелений
$work-color: #81a256;
$work-active-color: #a1ca6c;
