$contact-form-color: #68a097;
$contact-form-color: darken(desaturate($contact-color, 6.81), 6.47);
$contact-form-color: darken( #75b4aa, 6.47058823529413);
$footer-bg-color: #68a097;
$footer-bg-color: darken(desaturate($contact-color, 6.81), 6.47);
$footer-bg-icon-color: #82c7bc;
$footer-bg-icon-color: lighten(saturate($contact-color, 8.54), 6.27);
$footer-bg-icon-color: lighten( #75b4aa, 6.274509803921553 );


$button-color: #ff5151;
$bright-color: #ff5151;

$font-size: 10px;
$font-logo: 'Damion';
@mixin font-regular {
  font-family: 'Lato Regular', sans-serif;
  font-weight: normal; }
@mixin font-light {
  font-family: 'Lato Light', sans-serif;
  font-weight: 300; }
@mixin font-bold {
  font-family: 'Lato Bold', sans-serif;
  font-weight: bold; }
@mixin font-black {
  font-family: 'Lato Black', sans-serif;
  font-weight: 900; }
