@font-face {
  font-family: 'Damion';
  src: url('../fonts/Damion.eot');
  src: local('Damion'),
    url('../fonts/Damion.eot?#iefix') format('embedded-opentype'),
    url('../fonts/Damion.woff2') format('woff2'),
    url('../fonts/Damion.woff') format('woff'),
    url('../fonts/Damion.ttf') format('truetype'),
    url('../fonts/Damion.svg#Damion') format('svg');
  font-weight: normal;
  font-style: normal;
}

